import React, { Component } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import Documents from '../Documents';


class TabPlanAndSection extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tab1: 0,
            tab2: 0,
            isOpen: false,
        };
    }
    render() {
        const {column } = this.props;
        const { tab1, tab2, isOpen } = this.state;
        return (
            <div>
                <Tabs>
                    <div className="row text-center">
                        <div className="col-lg-12">
                            <div className="tablist-inner" 
                                style={{
                                    background: 'rgba(255, 255, 255, 1)',
                                    marginBottom: '30px',
                                    borderRadius: '20px'
                                }}
                            >
                                <TabList className="pv-tab-button text-center mt--0">
                                    <Tab><span>MUSEUM SEJARAH JAKARTA</span></Tab>
                                    <Tab><span>MUSEUM TEKSTIL</span></Tab>
                                    <Tab><span>MUSEUM SENI RUPA DAN KERAMIK</span></Tab>
                                    <Tab><span>MUSEUM WAYANG</span></Tab>
                                    <Tab><span>MUSEUM BAHARI</span></Tab>
                                </TabList>
                            </div>
                        </div>
                    </div>

                    <TabPanel className="">
                        
                    </TabPanel>
            
                    <TabPanel className="">
                        
                    </TabPanel>
                    <TabPanel className="">
                        <Documents url="/assets/msrk/FLOORPLAN_MSRK.pdf" w='800' warna='white'/>
                    </TabPanel>
                    <TabPanel className="">
                        <Documents url="/assets/mw/Museum Wayang - Floor Plan.pdf" w='800' warna='white'/>
                    </TabPanel>
                    <TabPanel className="">
                        <Documents url="/assets/mb/Museum Bahari - Floor Plan.pdf" w='800' warna='white'/>
                    </TabPanel>
                </Tabs>
            </div>
        )
    }
}


export default TabPlanAndSection
