                                        <p style={{color: 'white', fontWeight:'500'}}>Pada mulanya, bangunan Museum Tekstil Jakarta merupakan sebuah landhuis (villa) milik seorang warga negara Perancis yang tinggal di Batavia (Jakarta) serta dibangun sekitar awal abad ke-19. Bangunan tersebut kemudian dibeli oleh seorang konsul Turki bernama Sayed Abdul Aziz Al Kazimi lalu dijual kembali kepada Dr. Karel Christian Crucq pada tahun 1942. Bangunan Museum Tekstil Jakarta sempat digunakan sebagai markas besar Barisan Keamanan Rakyat (BKR) pada masa revolusi fisik di tahun 1945. Pada tahun 1952, Lie Sion Pin, sebagai pemiliki bangunan Museum Tekstil Jakarta pada saat itu menjual seluruh kawasan kepada Departemen Sosial RI sebagai tempat untuk menampung orang-orang jompo. Hingga pada tahun 1972, bangunan Museum Tekstil Jakarta akhirnya diresmikan sebagai bangunan bersejarah yang dilindungi oleh Undang-Undang Monumen STBL. 1931 No. 23 dan Surat Keputusan Gubernur DKI Jakarta No. CB. 11/1/12/72, tanggal 10 Januari 1972. Pada tahun 1975, bangunan Museum Tekstil Jakarta resmi diserahkan kepada Pemerintah DKI Jakarta untuk dialokasikan menjadi museum dan secara resmi dibuka pada tanggal 28 Juni 1976 oleh Ibu Tien Soeharto. Awal berdirinya Museum Tekstil Jakarta dipelopori oleh gagasan untuk mempertahankan kebudayaan kain tradisional di Indonesia karena mulai membanjirnya tekstil modern. Museum dengan luas 16.410 meter persegi ini terdiri dari tiga gedung utama yang dapat diakses oleh pengunjung diantaranya, gedung utama, gedung penunjang, dan gedung galeri.</p>
