import React, { useRef, useEffect, useState } from 'react';
import ScrollToTop from 'react-scroll-up';
import { FiChevronUp } from "react-icons/fi";
import Helmet from "../component/common/Helmet";
import TextLoop from "react-text-loop";
import HeaderThree from "../component/header/HeaderThree";
import FooterTwo from "../component/footer/FooterTwo";
import TabTwo from "../elements/tab/TabTwo";
import ContactThree from "../elements/contact/ContactThree";
import PortfolioList from "../elements/portfolio/PortfolioList";
import ServiceList from "../elements/service/ServiceList";
import BlogContent from "../elements/blog/BlogContent";
import GoogleMapReact from 'google-map-react';
import TabSOC from "../elements/tab/TabSOC";
import mapboxgl from 'mapbox-gl';
// import lokasiSite from '../LokasiSite.json'

mapboxgl.accessToken = 'pk.eyJ1IjoiZGltYXNhZHR5biIsImEiOiJjbHBhbm41M3MwOWFtMnBybGc5bGN6N3c3In0.TZsSqGom_oVHdALBcQDRNw';

const AnyReactComponent = ({ text }) => <div>{text}</div>;

const SlideList = [
    {
        textPosition: 'text-left',
        category: '',
        description: '',
        buttonText: '',
        buttonLink: ''
    }
]
const PortfolioLanding = () => {
    let title = 'About HBIM Jakarta',
        description = 'Heritage Building Information Modelling (HBIM) Jakarta adalah sistem terintegrasi pendokumentasian bangunan-bangunan cagar budaya yang berada di lingkup wilayah DKI Jakarta dengan menggunakan teknologi berbasis digital. Menyadari pentingnya pelestarian warisan budaya, tidak hanya untuk melindungi aspek fisik tetapi juga nilai penting yang dikandung, Dinas Kebudayaan DKI Jakarta sejak tahun 2022 mengaplikasikan HBIM dalam upaya pelestarian warisan budaya, khususnya pada bangunan dan struktur cagar budaya. Melalui sistem terintegrasi ini diharapkan dapat terwujud bentuk pengelolaan warisan budaya yang menyeluruh sekaligus membuka layanan akses informasi kepada publik dengan tampilan yang menarik dan informatif.';
    const PostList = BlogContent.slice(0 , 6);

    const mapContainer = useRef(null);
    const map = useRef(null);
    const [lng, setLng] = useState(106.813935);
    const [lat, setLat] = useState(-6.134457);
    const [zoom, setZoom] = useState(9);

    useEffect(() => {
        if (map.current) return; // initialize map only once
        map.current = new mapboxgl.Map({
            container: mapContainer.current,
            style: 'mapbox://styles/mapbox/streets-v12',
            center: [lng, lat],
            zoom: zoom
        });
         
    }, []);

    return (
        <div className="active-dark">
            <Helmet pageTitle="Portfolio Landing" />

            <HeaderThree homeLink="/" logo="symbol-dark" color="color-black"/>
            {/* Start Slider Area   */}
            <div id="home" className="fix">
                <div className="slider-wrapper">
                    {/* Start Single Slide */}
                    {SlideList.map((value , index) => (
                        <div className="slide personal-portfolio-slider slider-paralax slider-style-3 d-flex align-items-center justify-content-center" key={index} style={{backgroundImage: 'url(./assets/images/POINTCLOUDS/1.jpg)'}}>
                            
                                {/* <video className="w-100" autoPlay loop muted style={{zIndex: -1}}>
                                    <source src={'https://youtu.be/eMEhS8QeKaM?si=1WX2Hin2N5jvIgEz'} type='video/mp4' />
                                </video> */}
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className={`inner ${value.textPosition}`}>
                                            {value.category ? <span>{value.category}</span> : ''}
                                            <h1 className="title">HBIM JAKARTA<br/>
                                            <TextLoop>
                                                <span> HBIM.</span>
                                                <span> Point Clouds.</span>
                                                <span> 3D Scanning.</span>
                                            </TextLoop>{" "}
                                            </h1>
                                            {/* <h2>based in INDONESIA.</h2> */}
                                            {value.description ? <p className="description">{value.description}</p> : ''}
                                            {value.buttonText ? <div className="slide-btn"><a className="rn-button-style--2 btn-primary-color" href={`${value.buttonLink}`}>{value.buttonText}</a></div> : ''}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                    {/* End Single Slide */}
                </div>
            </div>
            {/* End Slider Area   */} 

            {/* Start About Area */}
            <div id="about" className="fix">
                <div className="about-area ptb--120  bg_color--1">
                    <div className="about-wrapper">
                        <div className="container">
                            <div className="row row--35 align-items-center">
                                <div className="col-lg-5">
                                    <div className="thumbnail">
                                        <img className="w-100" src="/assets/images/POINTCLOUDS/1.jpg" alt="About Images"/>
                                        <p/>
                                        <img className="w-100" src="/assets/images/POINTCLOUDS/2.jpg" alt="About Images"/>
                                        <p/>
                                        <img className="w-100" src="/assets/images/POINTCLOUDS/3.jpg" alt="About Images"/>
                                        {/* <video className="w-100" autoPlay loop muted id='video'>
                                            <source src={'https://www.youtube.com/watch?v=eMEhS8QeKaM&ab_channel=HBIMJakarta'} type='video/mp4' />
                                        </video> */}
                                    </div>
                                </div>
                                <div className="col-lg-7">
                                    <div className="about-inner inner">
                                        <div className="section-title">
                                            <h2 className="title">{title}</h2>
                                            <p className="description">{description}</p>
                                        </div>
                                        <div className="row mt--30">
                                            <TabTwo tabStyle="tab-style--1" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
            {/* End About Area */}

            {/* Start Maps Area */}
            <div id="maps" className="fix">
                <div className="about-area ptb--120  bg_color--1">
                    <div className="about-wrapper">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="section-title text-center service-style--3 mb--30 mb_sm--0">
                                        <h2 className="title">Maps</h2>
                                        <p>There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration.</p>
                                    </div>
                                </div>
                            </div>
                            <div style={{ height: '650px', width: '100%' }}>
                                {/* <GoogleMapReact
                                defaultCenter={{
                                    lat: -6.134457,
                                    lng: 106.813935
                                }}
                                defaultZoom={15}
                                >
                                <AnyReactComponent
                                    lat={-6.134457}
                                    lng={106.813935}
                                    text="My Marker"
                                />
                                </GoogleMapReact> */}
                                {/* <Map style="mapbox://styles/mapbox/streets-v8"/> */}
                                <div ref={mapContainer} style={{height: '600px', width:'100%'}}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
            {/* End maps Area */} 

            {/* Start Products Area  */}
            <div id="products" className="fix">
                <div className="service-area creative-service-wrapper ptb--120 bg_color--5" >
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title text-center service-style--3 mb--30 mb_sm--0">
                                    <h2 className="title">Our Products</h2>
                                    <p>There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration.</p>
                                </div>
                            </div>
                        </div>
                        <div className="row creative-service">
                            <div className="col-lg-12">
                                <ServiceList item="6" column="col-lg-4 col-md-6 col-sm-6 col-12 text-left" />
                            </div>
                        </div>
                    </div>
                </div>  
            </div>
            {/* End Products Area  */} 

            {/* Start Sites Area */}
            <div id="sites" className="fix">
                <div className="portfolio-area ptb--120 bg_color--1">
                    <div className="portfolio-sacousel-inner">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="section-title text-center service-style--3 mb--30 mb_sm--0">
                                        <h2 className="title">Sites</h2>
                                        <p>There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration.</p>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <PortfolioList styevariation="text-center mt--40" column="col-lg-4 col-md-6 col-sm-6 col-12" item="6" />
                            </div>
                            {/* <div className="row">
                                <div className="col-lg-12">
                                    <div className="view-more-btn mt--60 mt_sm--30 text-center">
                                        <a className="rn-button-style--2 btn-solid" href="/blog"><span>View More</span></a>
                                    </div>
                                </div>
                            </div> */}
                        </div>
                    </div>
                </div>
            </div>
            {/* End Sites Area */}

            {/* Start Archives Area */}
            <div id="archives" className="fix">
                <div className="rn-blog-area ptb--120 bg_color--5 mb-dec--30">
                    <div className="container">
                        <div className="row align-items-end">
                            <div className="col-lg-12 col-md-12 col-sm-12 col-12">
                                <div className="section-title text-center">
                                    <h2>Archives</h2>
                                    <p>There are many variations of passages of Lorem Ipsum available, <br />but the majority have suffered alteration.</p>
                                </div>
                            </div>
                        </div>
                        <div className="row mt--60 mt_sm--40">
                            {PostList.map((value , i ) => (
                                <div className="col-lg-4 col-md-6 col-12" key={i}>
                                    <div className="blog blog-style--1">
                                        <div className="thumbnail">
                                            <a href={value.url}>
                                                <img className="w-100" src={`/assets/images/blog/${value.images}.png`} alt="Blog Images"/>
                                            </a>
                                        </div>
                                        <div className="content">
                                            <p className="blogtype">{value.category}</p>
                                            <h4 className="title"><a href={value.url}>{value.title}</a></h4>
                                            <div className="blog-btn">
                                                <a className="rn-btn text-white" href={value.url}>Read More</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>    
                    </div>    
                </div>
            </div>
            {/* End Archives Area */}

            {/* Start SOC Area  */}
            <div id="soc" className="fix">
                <div className="service-area creative-service-wrapper ptb--120 bg_color--5" >
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title text-center service-style--3 mb--30 mb_sm--0">
                                    <h2 className="title">State of Conservations</h2>
                                </div>
                            </div>
                        </div>
                        <div className="designer-portfolio-area ptb--120 bg_color--1">
                            <div className="wrapper plr--70 plr_sm--30 plr_md--30">
                                <TabSOC column="col-lg-4 col-md-6 col-sm-6 col-12" />
                            </div>
                        </div>
                    </div>
                </div>  
            </div>
            {/* End Service Area  */}

            {/* Start Contact Area */}
            <div id="contact" className="fix">
                <div className="rn-contact-area ptb--120 bg_color--1">
                    <ContactThree contactImages="/assets/images/about/disbudprimary.png" contactTitle="Contact Us." />
                </div>
            </div>
            {/* End COntact Area */}

            <FooterTwo />
            {/* Start Back To Top */}
            <div className="backto-top">
                <ScrollToTop showUnder={160}>
                    <FiChevronUp />
                </ScrollToTop>
            </div>
            {/* End Back To Top */}
            
        </div>
    )
}

export default PortfolioLanding;
