import React, {useState} from 'react'
import { Document,Page } from 'react-pdf/dist/esm/entry.webpack';
import { FiChevronUp, FiChevronsDown } from "react-icons/fi";
// import './App.css';

function Documents({url, w, warna}) {

  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  function onDocumentLoadSuccess({numPages}){
    setNumPages(numPages);
    setPageNumber(1);
  }

  function changePage(offSet){
    setPageNumber(prevPageNumber => prevPageNumber + offSet);
  }

  function changePageBack(){
    changePage(-1)
  }

  function changePageNext(){
    changePage(+1)
  }

  return (
    <div className="App" >
      <center className="App-header">
        <Document file={url} onLoadSuccess={onDocumentLoadSuccess}>
          <Page width={w} pageNumber={pageNumber} />
        </Document>
        <p style={{color:`${warna}`}}> Page {pageNumber} of {numPages}</p>
        <div style={{color:{warna}, marginBottom:'30px'}}>
          { pageNumber > 1 && 
          <button onClick={changePageBack} style={{backgroundColorcolor:{warna}}}> 
            <p style={{color:`${warna}`}}> <FiChevronsDown color={warna}/>Previous Page</p>
          </button>
          }
          {
            pageNumber < numPages &&
            <button onClick={changePageNext} style={{backgroundColorcolor:{warna}}}> 
              
              <p style={{color:`${warna}`}}> <FiChevronUp color={warna}/>Next Page</p>
            </button>
          }
        </div>
      </center>
    </div>
  );
}

export default Documents;
