import React, { Component } from "react";
import {Link} from "react-router-dom";

const PortfolioListContent = [
    {
        image: 'image-1',
        category: 'Heritage Building',
        title: 'Museum Sejarah Jakarta',
        link: "/museum-sejarah-jakarta"
    },
    {
        image: 'image-2',
        category: 'Heritage Building',
        title: 'Museum Tekstil',
        link: '/museum-tekstil'
    },
    {
        image: 'image-3',
        category: 'Heritage Building',
        title: 'Museum Seni Rupa dan Keramik',
        link: 'museum-seni'
    },
    {
        image: 'image-4',
        category: 'Heritage Building',
        title: 'Museum Wayang',
        link: 'museum-wayang'
    },
    {
        image: 'image-5',
        category: 'Heritage Building',
        title: 'Museum Bahari',
        link: 'museum-bahari'
    },
    // {
    //     image: 'image-4',
    //     category: 'Heritage Building',
    //     title: 'Getting tickets to the big show'
    // }
]

class PortfolioList extends Component{
    render(){
        const {column , styevariation } = this.props;
        const list = PortfolioListContent.slice(0 , this.props.item);
        return(
            <React.Fragment> 
                {list.map((value , index) => (
                    <div className={`${column}`} key={index}>
                        <div className={`portfolio ${styevariation}`}>
                            <div className="thumbnail-inner">
                                <div className={`thumbnail ${value.image}`}></div>
                                <div className={`bg-blr-image ${value.image}`}></div>
                            </div>
                            <div className="content">
                                <div className="inner">
                                    <p>{value.category}</p>
                                    <h4><a href={value.link}>{value.title}</a></h4>
                                    <div className="portfolio-button">
                                        <a className="rn-btn" href={value.link}>View Details</a>
                                    </div>
                                </div>
                            </div>
                            <Link className="link-overlay" to={value.link}></Link>
                        </div>
                    </div>
                ))}
               
            </React.Fragment>
        )
    }
}
export default PortfolioList;